.container {
	max-width: 1180px;
	margin: 0 auto;
	padding: 0px 20px;
}

.header {
	width: 100%;
	height: 50px;
	position: fixed;
	background-color: tomato;
}

.header__body {
	display: flex;
	align-items: center;
	justify-content: space-between;
	height: 50px;
}

.signup_header:hover {
	background-color: #f15539;
	position: relative;
}

.signup_header__profile {
	display: flex;
	align-items: center;
	height: 50px;
	padding: 0px 8px;
	cursor: pointer;
}

.signup_header__name {
	color: #fff;
	font-size: 13px;
	letter-spacing: 1px;
	font-weight: 550;
}

.signup_header img {
	border-radius: 50%;
	display: block;
	margin: 0px 10px;
}

.signup_header__arrow {
	/*background: url(arrow-to-bottom.svg) 0 0 no-repeat;*/
	background-size: contain;
	width: 8px;
	height: 8px;
	margin-top: 8px;
}

.signup_header__menu {
	position: absolute;
	background-color: tomato;
	border-bottom: 1px solid #a70b00;
	border-right: 1px solid #a70b00;
	margin-top: 10px;
	padding: 10px 35px;
	margin-left: -60px;
	display: none;
}

.signup_header__link {
	color: #000;
	font-size: 13px;
	line-height: 27px;
}

.signup_header__link:hover {
	color: #a70b00;
}

.loginBlock a {
	color: white;
}
