.posts__box {
	background-color: #fff;
	border-radius: 5px;
	max-height: 50px;
	max-width: 90%;
	margin-left: 40px;
	cursor: text;
	border-bottom: 1px solid #cecece;
	display: flex;
	align-items: center;
	padding: 8px 20px;
}

.posts__box input {
	width: 100%;
	height: 100%;
}

.posts__user_link img {
	border-radius: 50%;
	margin-right: 10px;
}

.posts__field_wrap {
	display: flex;
	width: 100%;
	justify-content: space-between;
}

.posts__add_media li {
	cursor: pointer;
}

.posts__media_photo {
	width: 22px;
	height: 22px;
	/*background: url(post_icon.png) no-repeat;*/
	display: inline-block;
	background-position: 0 -66px;
}

.posts__media_video {
	width: 22px;
	height: 22px;
	/*background: url(post_icon.png) no-repeat;*/
	display: inline-block;
	background-position: 0 -88px;
}

.posts__media_audio {
	width: 22px;
	height: 22px;
	/*background: url(post_icon.png) no-repeat;*/
	display: inline-block;
	background-position: 0 -110px;
}

.posts__media_article {
	width: 22px;
	height: 22px;
	/*background: url(post_icon.png) no-repeat;*/
	display: inline-block;
	background-position: 0 -132px;
}

.posts__body button {
	width: 70px;
	height: 30px;
	background-color: teal;
	color: #fff;
	cursor: pointer;
	border-radius: 3px;
	margin-top: 6px;
	display: block;
	margin-left: auto;
	margin-right: 5.5%;
}
.posts__body button:hover {
	color: #ccc;
	border-right: 1px solid #ccc;
	border-bottom: 1px solid #ccc;
}
.posts__body button:active {
	color: tomato;
	border-right: 1px solid #ccc;
	border-bottom: 1px solid #ccc;
}
