*,
*:before,
*:after {
	padding: 0;
	margin: 0;
	border: 0;
	box-sizing: border-box;
}

html,
body {
	height: 100%;
	font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
}

a {
	text-decoration: none;
	cursor: pointer;
}

ul {
	margin-left: 0;
	padding-left: 0;
}

li {
	list-style-type: none;
}

