.my_posts {
	height: auto;
}

.my_posts__who {
	display: flex;
}

.my_posts__who img {
	border-radius: 50%;
}

.my_posts__title {
	display: flex;
	justify-content: space-between;
	width: 100%;
	align-items: center;
}

.my_posts__namedate {
	margin-left: 20px;
}

.my_posts__date {
	font-size: 12px;
	font-weight: 600;
	color: var(--color-grey);
	margin-top: 10px;
}

.my_posts__actions_arrow {
	/*background: url(./arrow-to-bottom-gr.svg) 0 0 no-repeat;*/
	background-size: contain;
	width: 10px;
	height: 10px;
	cursor: pointer;
}

.my_posts__hashtag {
	font-size: 12px;
	color: var(--color-grey);
	margin: 10px 0;
}

.my_posts__post {
	cursor: pointer;
	height: 500px;
	overflow: hidden;
}

.my_posts__post img {
	height: auto;
	width: 100%;
}

.my_posts__like {
	margin-top: 10px;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.my_posts__like_btns img {
	margin-right: 8px;
}

.my_posts__like_views span {
	font-size: 13px;
	color: var(--color-grey);
}

.posts__my_posts {
	margin: 20px 40px;
	background-color: #fff;
	border-radius: 5px;
	height: auto;
	border-bottom: 1px solid #cecece;
	padding: 10px 20px;
}
