.myfriends {
	padding-bottom: 20px;
	margin-left: 50px;
	display: grid;
	grid-template-areas: "avatar cont";
	grid-template-columns: 160px 1fr;
}

.avatar {
	grid-area: avatar;
	width: 150px;
	height: 150px;
	border-radius: 50%;
}

.content {
	grid-area: cont;
	margin-left: 20px;
}

.myfriends .name {
	font-size: 13px;
	font-weight: 600;
	color: teal;
	margin-right: 8px;
}

.myfriends p {
	margin-top: 5px;
}

.sendMessage {
	padding-top: 50px;
	font-size: 13px;
	color: #2a5885;
	font-weight: 500;
	letter-spacing: 1px;
}

.sendMessage:hover {
	color: tomato;
	cursor: pointer;
}
