.friends__topblock button {
	width: 120px;
	height: 30px;
	background-color: teal;
	color: #fff;
	cursor: pointer;
	border-radius: 3px;
	margin-top: 6px;
	display: block;
	margin-left: auto;
	margin-right: 5.5%;
}
.friends__topblock button:hover {
	color: #ccc;
	border-right: 1px solid #ccc;
	border-bottom: 1px solid #ccc;
}
.friends__topblock button:active {
	color: tomato;
	border-right: 1px solid #ccc;
	border-bottom: 1px solid #ccc;
}

.friends__searchblock input {
	display: block;
	width: 100%;
	margin: 20px 0px;
	padding: 10px 20px;
	border-top: 1px dashed tomato;
	border-bottom: 1px dashed tomato;
	background-color: #f3f4f7;
}
